                    ContactTime_m_time2: '',
                    ContactTime_m_time3: '',
                    m_Booking: '安排術檢',
                    parentName: '',
                    m_Referrer: null,
                    RaservationClinic: '',
                    RaservationTime: '',
                    RaservationDate: '',
                    RaservationRemark: '',
                    m_activityName: null,
                    type2_SourcePipeline: 1,
                },
                update: true,
                MyServicePhone: '',
            }
        },
        watch: { //偵聽
            ReservationCate: function (newValue, oldValue) {
                this.resetInput()
                this.GetClinicList_Lasik()
                if (this.CTI.CallInData[0]) {
                    this.formInput['cellphone'] = this.CTI.CallInData[0].fromPhoneNo
                    this.formInput['name'] = this.CTI.CallInData[0].name
                } else if (this.CTI.CustomerData[0]) {
                    this.formInput['cellphone'] = this.CTI.CustomerData[0].cellphone
                    this.formInput['name'] = this.CTI.CustomerData[0].name
                }
            },
            'CTI.CallInData': function (newValue, oldValue) {
                if (this.CTI.CallInData[0]) {
                    this.formInput['cellphone'] = this.CTI.CallInData[0].fromPhoneNo
                    this.formInput['name'] = this.CTI.CallInData[0].name
                } else if (this.CTI.CustomerData[0]) {
                    this.formInput['cellphone'] = this.CTI.CustomerData[0].cellphone
                    this.formInput['name'] = this.CTI.CustomerData[0].name
                }
            }
        },
        computed: {
            CustomerDataFilter: function () {
                let eBranch = []
                this.CTI.CustomerData.forEach((x) => {
                    eBranch.push({ "eBranchName": x.eBranchName, "dbName": x.dbName, "dbTypeUID": x.dbTypeUID, "cellphone": x.cellphone })
                })

                //排除物件陣列重複
                const set = new Set();
                const result = eBranch.filter(item => !set.has(item.eBranchName) ? set.add(item.eBranchName) : false);
                return result
            },
            todayformat: function () {
                var rightNow = new Date();
                var res = rightNow.toISOString().slice(0, 10)

                return res
            },
            Clinic_OKLensListFix: function () {
                let res = Object.values(this.Clinic_OKLensList).filter(x => x.Store != null)
                return res
            },
            ...mapState(['CTI']),
        },
        methods: {
          
            showMsgBoxTwo() {
                let _this = this
                this.$bvModal.msgBoxConfirm('確認送出嗎', {
                    title: 'EIP預約',
                    size: 'sm',
                    buttonSize: 'sm',
                    okVariant: 'danger',
                    okTitle: '確定',
                    cancelTitle: '取消',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
                    .then(value => {
                        if (value) {
                            _this.ReportResultSuccessMsg()
                        }
                    })
                    .catch(err => {
                        // An error occurred
                    })
            },
            ReportResultFailMsg(text) {
                this.$bvModal.msgBoxOk(text, {
                    title: '預約結果',
                    size: 'sm',
                    buttonSize: 'sm',
                    okVariant: 'danger',
                    okTitle: '確定',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
            },
            ReportResultSuccessMsg() {
                let _this = this
                this.$bvModal.msgBoxOk('提交成功，資料處理中請稍後', {
                    title: '回報結果',
                    size: 'sm',
                    hideFooter:true,
                    buttonSize: 'sm',
                    okTitleHtml: '',
                    okVariant:'d-none',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
            },
            resetInput() {
                for (var prop in this.formInput) {
                    this.formInput[prop] = '';
                    this.formInput['ContactTimeCateArray'] = [];
                    this.formInput['m_info1'] = [];
                    this.formInput['m_Booking'] = '安排術檢';
                    this.formInput['m_activityName'] = null; 
                    this.formInput['type2_SourcePipeline'] = 1;
                }
                //重新渲染元件
                let _this = this
                _this.update = false
                setTimeout(function () {
                    _this.update = true
                }, 1)
            },
            UpdateReportKind(payload) {
                this.ReportKind = payload
                if (payload == 3) {
                    this.ResetSetting()
                }
            },
            PushContactTimeCateArray(index) {
                if (index['Value'] == '任何時段') {
                    if (this.formInput.ContactTime_m_time0 == '') {
                        this.formInput.ContactTime_m_time0 = index['Value']
                    } else {
                        this.formInput.ContactTime_m_time0 = ''
                    }
                } else if (index['Value'] == '上午') {
                    if (this.formInput.ContactTime_m_time1 == '') {
                        this.formInput.ContactTime_m_time1 = index['Value']
                    } else {
                        this.formInput.ContactTime_m_time1 = ''
                    }
                } else if (index['Value'] == '下午') {
                    if (this.formInput.ContactTime_m_time2 == '') {
                        this.formInput.ContactTime_m_time2 = index['Value']
                    } else {
                        this.formInput.ContactTime_m_time2 = ''
                    }
                } else if (index['Value'] == '晚上') {
                    if (this.formInput.ContactTime_m_time3 == '') {
                        this.formInput.ContactTime_m_time3 = index['Value']
                    } else {
                        this.formInput.ContactTime_m_time3 = ''
                    }
                }
            },
            PushInfoFromArray(index) {
                let item = this.formInput.m_info1.findIndex(x => x === index['Value']);
                if (item > -1) {
                    this.formInput.m_info1.splice(item, 1);
                } else {
                    this.formInput.m_info1.push(index['Value'])
                }
            },
            GetClinicList_Lasik() {
                this.Clinic_LasikList = ClinicList
                 this.Clinic_OKLensList = ClinicList
            },
            GetActiveList(id) {
                let _this = this
                fetch(GetActiveListUrl + id, {
                    method: 'POST',
                }).then(function (response) {
                    return response.json();
                })
                .then(function (myJson) {
                    _this.ActiveList = JSON.parse(myJson.responseData)
                });
            },
            UpDateCallInDTransToEIP(CallinID) {
                fetch(UpDateCallInDTransToEIPUrl, {
                    headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({ CallinID: CallinID }),
                    method: 'POST',
                })
            }
        },
        mounted() {
            let storage = JSON.parse(localStorage.getItem("UserInfo"));
            this.UserInfo = storage
            this.MyServicePhone = this.$route.params.ServicePhone
            this.ReservationCate = 'Lasik'

        },
        components: {
            //ReportBtn,
            ReportItemBtn,
            datetime: Datetime
        }
    }
</script>
