<template>
    <b-modal id="LineMessagePopup" @show="startData()" @hide="cleanData(),LineMessageList.item = [], LineMessageList.status = ''" centered title="" hide-header hide-footer size="lg" content-class="model-bg-gray">
        <div class="header d-flex align-items-center justify-content-between">
            <div class="ml-2 closeBtn cursorPoint" @click="closeModel()"><b-icon icon="x"></b-icon></div>
            <div class="d-flex align-items-center messageTitle">
                <img class="mr-2" src="../assets/LineIcon.svg" />
                <span class="white-text" v-if="LineData[0]">對話紀錄 - {{LineData[0].displayName}}</span>
            </div>
            <div class="mr-2">
                {{UserInfo.deptName}} - {{UserInfo.fullName}}
            </div>
        </div>
        <div class="content" id="LineMessageContent">
            <div v-if="LineMessageList.item.length == 0 && LineMessageList.status == 204">
                尚無對話訊息
            </div>
            <div class="loading" v-if="LineMessageList.item.length == 0 && LineMessageList.status !== 204">
                 載入中請稍後 <b-icon icon="three-dots" class="ml-2" animation="cylon" font-scale="4"></b-icon>
            </div>
            <div v-else>
                <div v-for="(item,index) in closeDate" :key="index">
                    <div class="d-flex align-items-center">
                        <hr class="dateHR" />
                        <span class="pr-4 pl-4 light-gray">{{item.messageDate}}</span>
                        <hr class="dateHR" />
                    </div>
                    <div v-for="(items,index) in LineMessageList.item" :key='index' v-show="items.chatFormID == item.chatFormID">
                        <div class="MessageWrap d-flex justify-content-end align-items-center" v-if="items.empolyeeMsg != '' || items.empolyeeImg != ''">
                            <div class="d-flex flex-column align-items-end mb-3">
                                <div class="mb-2 gray-text">{{items.empolyeeName}}</div>
                                <div class="d-flex align-items-end">
                                    <span class="time sm-text">{{items.createDT.slice(11,16)}}</span>
                                    <div class="msgBox  EmpMessage">
                                        <span>{{items.empolyeeMsg}}</span>
                                        <a v-if="items.empolyeeImg" :href="'https://linebot.eyedoctor.com.tw/folder/' + items.empolyeeImg" target="_blank">
                                            <img width="120" height="120" :src="'https://linebot.eyedoctor.com.tw/folder/' + items.empolyeeImg" alt="載入失敗">
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <!--<div class="Pic">
                               <img :src="items.empolyeePictureUrl" @error="empolyeeimgError(items)" />
                            </div>-->
                        </div>
                        <div class="MessageWrap d-flex align-items-center" v-if="items.customerMsg != '' || items.customerstickerId != '' || items.customerImg != ''">
                            <div class="Pic">
                                <img :src="items.customerPictureUrl" @error="customerimgError(items)" />
                            </div>
                            <div class="msgBox CustomerMessage" v-if="items.customerMsg || items.customerstickerId">
                                {{items.customerMsg ? items.customerMsg : items.customerstickerId}}
                            </div>
                            <div class="msgBox CustomerMessage" v-else-if="items.customerImg">
                                <a :href="'https://linebot.eyedoctor.com.tw/folder/' + items.customerImg + '.jpg'" target="_blank">
                                    <img width="120" height="120" :src="'https://linebot.eyedoctor.com.tw/folder/' + items.customerImg + '.jpg'" alt="載入失敗">
                                </a>
                            </div>
                            <span class="time sm-text">{{items.createDT.slice(11,16)}}</span>
                        </div>
                    </div>
                    <div class="CloseDate" v-if="item.closeDate ">
                        以上對話於 {{item.closeDate}} 已結案
                    </div>
                </div>
            </div>
            <div class="ScrollBottom" @click="ScrollToBottom">
                <b-icon icon="arrow-down"></b-icon>
            </div>
        </div>
        <div class="footer d-flex">
            <textarea class="PushMessage" placeholder="請輸入內容" v-model="ReplayText" id="PushMessage"></textarea>
            <div class="btnTool d-flex align-items-center">
                <label for="PushFile" class="cursorPoint disabled">
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" clip-rule="evenodd" d="M12.6399 3.5C12.0817 3.5 11.5464 3.71765 11.1517 4.10507L4.56465 10.5709C3.90097 11.2224 3.52813 12.1059 3.52813 13.0272C3.52813 13.9485 3.90097 14.8321 4.56465 15.4836C5.22832 16.135 6.12846 16.501 7.06703 16.501C8.00561 16.501 8.90575 16.135 9.56942 15.4836L16.1565 9.01773C16.4549 8.72484 16.9386 8.72484 17.237 9.01773C17.5354 9.31063 17.5354 9.7855 17.237 10.0784L10.65 16.5442C9.69972 17.477 8.4109 18.001 7.06703 18.001C5.72317 18.001 4.43435 17.477 3.4841 16.5442C2.53385 15.6115 2 14.3464 2 13.0272C2 11.7081 2.53385 10.443 3.4841 9.51023L10.0712 3.04441C10.7524 2.37569 11.6764 2 12.6399 2C13.6033 2 14.5273 2.37569 15.2086 3.04441C15.8898 3.71314 16.2726 4.62013 16.2726 5.56585C16.2726 6.51157 15.8898 7.41856 15.2086 8.08729L8.61436 14.5531C8.61426 14.5532 8.61446 14.553 8.61436 14.5531C8.20211 14.9576 7.64278 15.1852 7.05987 15.1852C6.47682 15.1852 5.91765 14.9578 5.50537 14.5531C5.0931 14.1484 4.86148 13.5995 4.86148 13.0272C4.86148 12.4549 5.0931 11.906 5.50537 11.5013L11.591 5.53474C11.8896 5.24202 12.3733 5.2423 12.6716 5.53537C12.9698 5.82843 12.9695 6.30331 12.6709 6.59603L6.58592 12.562C6.46022 12.6854 6.38961 12.8527 6.38961 13.0272C6.38961 13.2017 6.46022 13.3691 6.58592 13.4924C6.71162 13.6158 6.8821 13.6852 7.05987 13.6852C7.23763 13.6852 7.40811 13.6158 7.53381 13.4924L14.128 7.02663C14.5227 6.63921 14.7445 6.11375 14.7445 5.56585C14.7445 5.01795 14.5227 4.4925 14.128 4.10507C13.7333 3.71765 13.198 3.5 12.6399 3.5Z" fill="#93959C" />
                    </svg>
                    <input class="d-none" id="PushFile" type="file" value="" disabled />
                </label>
                <label for="PushSticker" class="cursorPointr disabled">
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" clip-rule="evenodd" d="M10.15 2C5.64883 2 2 5.58167 2 10C2 14.4183 5.64883 18 10.15 18C14.6503 18 18.3 14.4183 18.3 9.99917C18.3 5.58167 14.6503 2 10.15 2ZM10.15 16.3325C6.58694 16.3325 3.69794 13.4975 3.69794 9.99917C3.69794 6.50083 6.58609 3.66667 10.15 3.66667C13.7131 3.66667 16.603 6.50167 16.603 10C16.603 13.4983 13.7131 16.3325 10.15 16.3325ZM9.30102 8.33333C9.30102 9.13917 8.73137 9.79167 8.02843 9.79167C7.32465 9.79167 6.75415 9.13917 6.75415 8.33333C6.75415 7.5275 7.32465 6.875 8.02759 6.875C8.73053 6.875 9.30102 7.5275 9.30102 8.33333ZM12.2724 9.79167C12.9762 9.79167 13.5458 9.13917 13.5458 8.33333C13.5458 7.5275 12.9753 6.875 12.2724 6.875C11.5695 6.875 10.999 7.52833 10.999 8.33333C10.999 9.13833 11.5695 9.79167 12.2724 9.79167ZM12.9796 11.38C13.1434 11.0775 13.5272 10.9583 13.8353 11.1133C14.146 11.27 14.2717 11.6392 14.1155 11.945C14.0739 12.0275 13.0585 13.9575 10.1508 13.9575C7.24402 13.9575 6.22697 12.0283 6.18452 11.9458C6.02746 11.6367 6.1548 11.2617 6.46892 11.1067C6.78473 10.9525 7.16167 11.0767 7.32127 11.3808C7.35693 11.4467 8.07005 12.7075 10.1508 12.7075C12.2418 12.7075 12.9507 11.435 12.9796 11.38Z" fill="#93959C" />
                    </svg>
                    <input class="d-none" id="PushSticker" type="file" value="" disabled />
                </label>
                <label for="PushImg" class="cursorPoint">
                    <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" clip-rule="evenodd" d="M4.40538 3.5C3.92089 3.5 3.52812 3.88553 3.52812 4.36111V15.6389C3.52812 16.0209 3.78148 16.3447 4.13223 16.4574L12.8923 7.85856C13.1907 7.56566 13.6745 7.56566 13.9729 7.85856L16.7719 10.606V4.36111C16.7719 3.88553 16.3791 3.5 15.8946 3.5H4.40538ZM16.7719 12.7273L13.4326 9.44955L6.24996 16.5H15.8946C16.3791 16.5 16.7719 16.1145 16.7719 15.6389V12.7273ZM2 4.36111C2 3.05711 3.07693 2 4.40538 2H15.8946C17.2231 2 18.3 3.05711 18.3 4.36111V15.6389C18.3 16.9429 17.2231 18 15.8946 18H4.40538C3.07693 18 2 16.9429 2 15.6389V4.36111ZM7.2777 6.7222C7.01982 6.7222 6.81077 6.9274 6.81077 7.18053C6.81077 7.43366 7.01982 7.63887 7.2777 7.63887C7.53557 7.63887 7.74463 7.43366 7.74463 7.18053C7.74463 6.9274 7.53557 6.7222 7.2777 6.7222ZM5.28265 7.18053C5.28265 6.09897 6.17586 5.2222 7.2777 5.2222C8.37953 5.2222 9.27275 6.09897 9.27275 7.18053C9.27275 8.26209 8.37953 9.13887 7.2777 9.13887C6.17586 9.13887 5.28265 8.26209 5.28265 7.18053Z" fill="#93959C" />
                    </svg>
                    <input class="d-none" id="PushImg" type="file" value="" accept="image/*" ref="PushImg" @change="previewFile()" />
                </label>
                <div :class="['submitBtn','cursorPoint' ,{disabled : (ImgFileName == '' && ReplayText == '')  || IsDisabled}]" 
                     :disabled="(ImgFileName == '' && ReplayText == '') || IsDisabled" @click="PushMessage()">
                    送出
                </div>
                <div v-if="CTI.LineAtData && isFinisBtn" 
                     :class="['finishBtn' ,'cursorPoint',{disabled : isFinisBtn.closeDate !== null}]"
                     @click="showMsgBoxTwo()"
                    >結案</div>
                <div v-if="ImgFileName" class="previewPic" @click="cleanImg()">
                    <img :src="ImgBase64" width="60" height="60" alt="" />
                </div>
            </div>
        </div>
    </b-modal>
</template>
<script>
    const postUrl = '/GetDataApi/EmpolyeePush_Message'
    const GetLineMessageUrl = '/GetDataApi/GetLineMessage'
    const CloseChatFormUrl = '/GetDataApi/CloseChatForm'
    import { mapState } from 'vuex'
    let timer
    export default {
        props:['LineAccountID'],
        data() {
            return {
                IsDisabled:false,
                showScroll:null,
                EmpolyeeUserID: '',
                ReplayText: '',
                ImgBase64: '',
                ImgFileName: '',
                LineMessageList: {
                    item: [],
                    status:'',
                },
                LineData: '',
                UserInfo: '',
                getMessageNum:0
            }
        },
        computed: {
            ...mapState(['CTI']),
           
            messageDate: function () {
                let arr = this.LineMessageList.item
                const set = new Set();
                const result = arr.filter(item => !set.has(item.messageDate) ? set.add(item.messageDate) : false);
                return result
            },
            closeDate: function () {
                 let arr = this.LineMessageList.item
                const set = new Set();
                const result = arr.filter(item => !set.has(item.chatFormID) ? set.add(item.chatFormID) : false);
                return result
            },
            isFinisBtn: function () {
                let arr = this.LineMessageList.item
                const set = new Set();
                const result = arr.filter(item => !set.has(item.chatFormID) ? set.add(item.chatFormID) : false);
                let item = result.reverse()[0]
                return item
            }
        },
        watch: {
            'LineMessageList.item': function (newValue, oldValue) {
               
            },
        },
        mounted() {
            let storage = JSON.parse(localStorage.getItem("UserInfo"));
            this.UserInfo = storage
            this.EmpolyeeUserID = storage.empUserId
        },
        created() {
          },
        methods: {
             //用lineAccountID 取得LINE userdata 
            GetLineData(AccountID) {
                let _this = this
                setTimeout(() => {
                    document.getElementById("PushMessage").focus()
                },1000)
                this.LineData = this.CTI.LineAtData.filter(checkCate)
                function checkCate(item) {
                    return item.accountID == AccountID
                }
            },
            cleanImg() {
                this.ImgBase64 = '',
                this.ImgFileName = '',
                this.$refs.PushImg.value = ''
            },
            resetMessage() {
                this.cleanImg()
                this.ReplayText = ''
            },
            openModel() {
                this.$bvModal.show('LineMessagePopup')
            },
            closeModel() {
                this.$bvModal.hide('LineMessagePopup')
               
            },
             //預覽圖片
            previewFile() {
                let _this = this
                const files = this.$refs.PushImg.files[0];
                const reader = new FileReader();
                this.ImgFileName = files.name;

                reader.addEventListener("load", function () {
                    // convert image file to base64 string
                    _this.ImgBase64 = reader.result;
                    
                }, false);

                if (files) {
                    reader.readAsDataURL(files);
                }
            },
            ScrollToBottom() {
                let objDiv = document.getElementById("LineMessageContent");
               
                if (objDiv != null) {
                     objDiv.scrollTop = objDiv.scrollHeight;
                }
               
            },
             //取得對話紀錄
            GetLineMessage() {
                let userID
                if (this.LineData.length > 0) {
                    userID = this.LineData[0].userId
                }

                if (userID) {
                    fetch(`${GetLineMessageUrl}/?CustomerLineID=${userID}`, {
                        method: 'POST',
                    }).then((response) => {
                        this.LineMessageList.status = response.status
                        return response.json();
                    }).then((jsonData) => {
                        this.LineMessageList.item = jsonData
                        this.getMessageNum += 1
                        if (this.getMessageNum <= 2) {
                             this.ScrollToBottom()
                        }
                        return jsonData
                    }).catch(err => {
                        return err
                    })
                }
               
            },
            //發送訊息給客人
            PushMessage() {
                let CustomerUserID = this.LineData[0].userId
                this.IsDisabled = true
                fetch(postUrl, {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        CustomerUserID: CustomerUserID,
                        EmpolyeeUserID: this.EmpolyeeUserID,
                        ReplayText: this.ReplayText,
                        ImgBase64: this.ImgBase64.replace(/^data:image\/[a-z]+;base64,/, ""),
                        ImgFileName: this.ImgFileName
                    }),
                    method: 'POST',
                }).then((response) => {
                    return response.json();
                }).then((jsonData) => {
                    this.resetMessage()
                     this.IsDisabled = false
                   return jsonData
                }).catch(err => {
                     this.ReportResultFailMsg()
                    return err
                })
            },
            CloseChatForm() {
                 let CustomerUserID = this.LineData[0].userId
                fetch(CloseChatFormUrl, {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        CustomerUserID: CustomerUserID,
                    }),
                    method: 'POST',
                }).then((response) => {
                    return response.json();
                }).then((jsonData) => {
                    this.resetMessage()
                    return jsonData
                }).catch(err => {
                    this.ReportResultFailMsg()
                    return err
                })
            },
            startData() {
                let _this = this
                _this.GetLineMessage()

                 timer = setInterval(() => {
                    _this.GetLineMessage()
                },3000)
            },
            cleanData() {
                clearInterval(timer)
                this.getMessageNum = 0
            },
             showMsgBoxTwo() {
                let _this = this
                this.$bvModal.msgBoxConfirm('您將結束該次對話', {
                    title: '確認結案',
                    size: 'sm',
                    buttonSize: 'sm',
                    okVariant: 'danger',
                    okTitle: '確定',
                    cancelTitle: '取消',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
                    .then(value => {
                        if (value) {
                            _this.CloseChatForm()
                        }
                    })
                    .catch(err => {
                        // An error occurred
                    })
            },
            ReportResultFailMsg() {
                this.$bvModal.msgBoxOk('發生錯誤，請洽總部資訊人員', {
                    title: '錯誤',
                    size: 'sm',
                    buttonSize: 'sm',
                    okVariant: 'danger',
                    okTitle: '確定',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
            },
             empolyeeimgError(item) {
                item.empolyeePictureUrl = require('../assets/DefaultPic.png')
            },
              customerimgError(item) {
                item.customerPictureUrl = require('../assets/DefaultPic.png')
            },
        }
    }
</script>
<style >
    #LineMessagePopup .closeBtn {
        background: #30323B;
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
    }

        #LineMessagePopup .closeBtn .b-icon.bi {
            color: #93959C !important;
            font-size: 16px !important;
        }

    .loading .b-icon.bi {
        color: #93959C !important;
        font-size: 16px !important;
    }

    .previewPic {
        position: absolute;
        left: 0;
        top: -62px;
        border: 1px solid #505359;
        background: #1d1e24;
    }

        .previewPic:hover:after {
            /* -webkit-filter: grayscale(1); */
            background: #000000a8;
            content: "刪除";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
        }

    .submitBtn {
        width: 70px;
        height: 40px;
        background: #0090FF;
        border-radius: 15px;
        display:flex;
        align-items:center;
        justify-content:center;
    }
    .btnTool .disabled {
        opacity: 0.3;
        cursor: not-allowed;
        pointer-events: none;
    }
    

    .finishBtn {
        width: 70px;
        height: 40px;
        background: #00A583;
        border-radius: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .btnTool {
        width: 290px;
        justify-content: space-evenly;
    }
    .light-gray {
        color: #93959C;
    }

    #LineMessagePopup .ScrollBottom {
        position: absolute;
        right: 20px;
        bottom: 100px;
        width: 35px;
        height: 35px;
        background: #4a4a4a99;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }
        #LineMessagePopup .ScrollBottom .b-icon.bi {
            font-size: 20px !important;
            color: #848484 !important;
        }

        #LineMessagePopup .PushMessage {
            background: transparent;
            border: 0px;
            width: 290px;
            color: #FCFCFC;
        }

    #LineMessagePopup .modal-content {
        border-radius: 20px;
    }

        #LineMessagePopup .modal-dialog{
            width:620px;
        }

        #LineMessagePopup .modal-body {
            padding: 0;
        }

        #LineMessagePopup .header {
            padding: 20px;
            position:relative;
           
        }

    #LineMessagePopup .content {
        padding: 20px 20px 60px 20px;
        border-top: 1px solid #505359;
        border-bottom: 1px solid #505359;
        height: 450px;
        max-height: 450px;
        overflow-y: scroll;
        transition: .3s;
    }

        #LineMessagePopup .content::-webkit-scrollbar {
            width: 5px;
            background-color: #23262b;
            cursor: pointer;
        }

        #LineMessagePopup .content::-webkit-scrollbar-thumb {
            border-radius: 10px;
            background-color: #1d1e24;
        }


        #LineMessagePopup .content:hover::-webkit-scrollbar {
            width: 5px;
            background-color: #23262b;
            cursor: pointer;
        }

        #LineMessagePopup .content:hover::-webkit-scrollbar-thumb {
            border-radius: 10px;
            background-color: #676769;
        }

        #LineMessagePopup .messageTitle {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
        }

        #LineMessagePopup .dateHR {
            height: 0.5px;
            background: #33363D;
            width: 35%;
        }

        #LineMessagePopup .msgBox {
            padding: 10px 15px;
            margin: 15px;
        }

            #LineMessagePopup .msgBox.EmpMessage {
                background: #86D97B;
                color: #505359;
                border-radius: 15px 0px 0px 15px;
                display: flex;
                flex-direction: column;
                margin: 0 0 0 15px;
            }

            #LineMessagePopup .msgBox.CustomerMessage {
                background: #131313;
                color: #FCFCFC;
                border-radius: 0px 15px 15px 0px;
                display: flex;
                flex-direction: column;
            }

        #LineMessagePopup .time {
            color: #505359;
        }

        #LineMessagePopup .Pic{
            width:35px;
            height:35px;
            border-radius:50%;
            overflow:hidden;
        }

        #LineMessagePopup .Pic img{
            width:100%;
        }

    #LineMessagePopup .footer{
        position:relative;
        padding:20px;
    }

    #LineMessagePopup .CloseDate {
        background: #00A538;
        width: 100%;
        height: 40px;
        margin:20px 0 ;
        display:flex;
        justify-content:center;
        align-items:center;
    }
</style>