<template>
    <div>
        <b-modal id="ReportModal" ref="ReportModal" @hide="reset()" centered title="" hide-footer size="xl" footer-class="model-bg-gray" content-class="model-bg-gray ReportModal">
            <div class="title">
                <span class=" mr-3">未接待處理</span>{{ReportModalData.IVRTitle}}
            </div>
            <div class="d-flex" v-if="CTI.CallInData[0]">
                <div class="d-flex flex-column align-items-start col-6">
                     <div class="box d-flex align-items-center">
                            <div class="ProfileCircle mr-3">
                                <img src="../assets/CustomerDefaultPic.png" />
                            </div>
                            <div class="d-flex align-items-end flex-wrap white-text">
                                <p class="mb-0 lg-text pr-1">{{CTI.CallInData[0].customerID == 0 ? '多筆資料' : CTI.CallInData[0].customerID == null ? '新客戶' : CTI.CallInData[0].name}}</p>
                                <span class="pb-1 pr-1"
                                      v-if="CTI.CallInData[0].customerID != 0 || CTI.CallInData[0].customerID != null">
                                {{CTI.CallInData[0].sex == 1 ? '先生' : CTI.CallInData[0].sex  == 2 ? '小姐' : '先生/小姐'}}
                                </span>
                                <span class="pb-1" v-if="CTI.CallInData[0].age">{{CTI.CallInData[0].age}} 歲</span>
                            </div>
                        </div>
                        <div class="content">
                            <div v-for="(item , index) in tagName.data" class="tagNameWrap mt-4" :key="index">
                                <div class="tagName normalTag">
                                    <span>
                                        <svg width="12" height="13" viewBox="0 0 12 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M12 4.77254V1.0043C11.972 0.428556 11.5207 0.00693333 11.023 0H7.35701C6.76746 0.0376566 6.06356 0.275242 5.68552 0.713808L0.259268 6.92227C-0.081584 7.36581 -0.0912275 7.97959 0.259268 8.34986L4.5066 12.7157C4.92305 13.103 5.54441 13.0865 5.89546 12.7157L11.3056 6.49065C11.7 6.01189 11.9946 5.3718 12 4.77254ZM10.4093 2.59792C10.3862 3.15811 9.95625 3.5624 9.47259 3.56903C8.92604 3.54394 8.5343 3.09614 8.52784 2.59792C8.5519 2.03585 8.98977 1.64163 9.47259 1.63511C10.0178 1.65852 10.403 2.10271 10.4093 2.59792Z" fill="#B5B7BF" />
                                        </svg>
                                    </span> {{item.tagName}}
                                </div>
                            </div>
                        </div>
                    <hr class="gray-hr" />
                     <div class="d-flex">
                        <span class="gray-text mr-3">來電時間</span>
                        <span class="blue-text">{{CTI.CallInData[0].dialDT.slice(5,16)}}</span>
                    </div>
                     <!--<hr class="gray-hr" />
                    <div>
                        <span class="gray-text">執行動作</span><br>
                        <div class="CallOutIcon mt-2" @click="info(CallInData[0].fromPhoneNo,CallInData[0].callinID)">
                            <img width="24" height="22" src="../assets/CallInStatusIcon.png" />
                        </div>
                    </div>-->
                     <hr class="gray-hr" />
                    <div v-if="JsonAgentDial">
                        <div class="gray-text pb-3">漏接點</div>
                        <div class="d-flex">
                            <div v-for="(item,index) in JsonAgentDial" class="mr-3" :key="index">
                                <div class="d-flex flex-column align-items-center" v-if="item.Location == '行銷部'">
                                    <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <circle cx="15" cy="15" r="14.5" stroke="#93959C" />
                                        <path d="M12.4272 16.9238L11.1952 12.326L19.2807 7.65278C19.8501 7.32364 20.5768 7.62443 20.747 8.25974L22.795 15.9029C22.9679 16.5482 22.4716 17.1784 21.8037 17.1614L12.4272 16.9238Z" stroke="#93959C" />
                                        <path d="M13.802 22.0542L12.4272 16.9234L9.7951 17.6287L11.1699 22.7595L13.802 22.0542Z" stroke="#93959C" />
                                        <path d="M7.96593 13.1911L11.1953 12.3258L12.4272 16.9236L9.19789 17.7889C8.66443 17.9318 8.11609 17.6153 7.97315 17.0818L7.25882 14.4159C7.11588 13.8824 7.43246 13.3341 7.96593 13.1911Z" stroke="#93959C" />
                                    </svg>
                                    <div class="mt-1 white-nowrap">{{item.Location}}</div>
                                    <div >{{item.Agent}}</div>
                                </div>
                                <div class="d-flex flex-column align-items-center" v-else-if="item.Location == '藥局'">
                                    <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <circle cx="15" cy="15" r="14.5" stroke="#93959C" />
                                        <path fill-rule="evenodd" clip-rule="evenodd" d="M14.7191 10.3563C15.4897 9.05932 17.1965 8.61493 18.5312 9.36376C19.8659 10.1126 20.3232 11.7711 19.5526 13.0681L15.6458 19.6437C14.8752 20.9407 13.1684 21.3851 11.8337 20.6362C10.499 19.8874 10.0417 18.2289 10.8123 16.9319L14.7191 10.3563ZM16.8646 16.1338L18.8973 12.7126C19.4675 11.753 19.1242 10.5314 18.1497 9.98464C17.1828 9.44219 15.9405 9.75906 15.3744 10.7118L13.3276 14.1568L16.8646 16.1338Z" fill="#93959C" />
                                    </svg>
                                    <div class="mt-1 white-nowrap">{{item.Location}}</div>
                                    <div >{{item.Agent}}</div>
                                </div>
                                <div class="d-flex flex-column align-items-center" v-else-if="item.Location == '員休室'">
                                    <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <circle cx="15" cy="15" r="14.5" stroke="#93959C" />
                                        <path d="M14.3333 10.8V12.2C13.5486 12.2 12.7812 12.4424 12.1267 12.897C11.4722 13.3516 10.9595 13.9983 10.6524 14.7565C10.3454 15.5148 10.2575 16.3511 10.3998 17.1615C10.542 17.9718 10.9082 18.7202 11.4526 19.3137C11.997 19.9071 12.6956 20.3193 13.4614 20.499C14.2273 20.6786 15.0265 20.6177 15.7597 20.324C16.4929 20.0302 17.1275 19.5164 17.5847 18.8467C18.0418 18.177 18.3012 17.3809 18.3307 16.5575L18.3333 16.4H19.6667C19.6667 17.8852 19.1048 19.3096 18.1046 20.3598C17.1044 21.41 15.7478 22 14.3333 22C12.9188 22 11.5623 21.41 10.5621 20.3598C9.5619 19.3096 9 17.8852 9 16.4C9 13.3655 11.3867 10.8 14.3333 10.8ZM21 8V9.4L17.4487 13.6H21V15H15.6667V13.6L19.2173 9.4H15.6667V8H21Z" fill="#93959C" />
                                    </svg>
                                    <div class="mt-1 white-nowrap">{{item.Location}}</div>
                                    <div >{{item.Agent}}</div>
                                </div>
                                <div class="d-flex flex-column align-items-center" v-else-if="item.Location == '櫃台'">
                                    <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <circle cx="15" cy="15" r="14.5" stroke="#93959C" />
                                        <path d="M14.3656 20.1665C13.7195 20.155 13.091 20.1036 12.4802 20.0122C11.8693 19.9094 11.3759 19.7723 11 19.601V18.1444C11.3994 18.3272 11.9104 18.4871 12.533 18.6242C13.1557 18.7613 13.7665 18.8356 14.3656 18.847V15.3856C13.2026 15.0771 12.351 14.6887 11.8106 14.2203C11.2702 13.7519 11 13.1122 11 12.3011C11 11.4557 11.2996 10.7931 11.8987 10.3133C12.5095 9.83354 13.3319 9.56508 14.3656 9.50796V8H15.4934V9.49082C16.116 9.50224 16.6799 9.57079 17.185 9.69645C17.7019 9.81069 18.1718 9.95349 18.5947 10.1248L18.1189 11.3929C17.743 11.2444 17.326 11.1187 16.8678 11.0159C16.4214 10.9017 15.9633 10.8274 15.4934 10.7931V14.2375C16.2687 14.4545 16.9148 14.6887 17.4317 14.94C17.9486 15.1799 18.3363 15.4827 18.5947 15.8482C18.8649 16.2024 19 16.665 19 17.2362C19 18.0359 18.6946 18.6871 18.0837 19.1897C17.4728 19.6809 16.6094 19.9837 15.4934 20.0979V22H14.3656V20.1665ZM14.3656 10.8274C13.7313 10.8731 13.2614 11.0159 12.9559 11.2558C12.6623 11.4957 12.5154 11.8042 12.5154 12.1812C12.5154 12.6381 12.6446 12.9922 12.9031 13.2436C13.1733 13.4835 13.6608 13.6891 14.3656 13.8605V10.8274ZM15.4934 18.7613C16.1865 18.6928 16.6916 18.5386 17.0088 18.2987C17.326 18.0473 17.4846 17.7275 17.4846 17.339C17.4846 16.9049 17.3436 16.5794 17.0617 16.3623C16.7797 16.1338 16.257 15.9282 15.4934 15.7454V18.7613Z" fill="#93959C" />
                                    </svg>
                                    <div class="mt-1 white-nowrap">{{item.Location}}</div>
                                    <div >{{item.Agent}}</div>
                                </div>
                                <div class="d-flex flex-column align-items-center" v-else-if="item.Location == '諮詢室'">
                                    <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <circle cx="15" cy="15" r="14.5" stroke="#93959C" />
                                        <path d="M14.1404 20H15.8596V13.0949H14.1404V20ZM15.0058 11.9852C15.5906 11.9852 16 11.5906 16 10.9864C16 10.3946 15.5906 10 15.0058 10C14.4094 10 14 10.3946 14 10.9864C14 11.5906 14.4094 11.9852 15.0058 11.9852Z" fill="#93959C" />
                                    </svg>
                                    <div class="mt-1 white-nowrap">{{item.Location}}</div>
                                    <div >{{item.Agent}}</div>
                                </div>
                                <div class="d-flex flex-column align-items-center" v-else>
                                    <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <circle cx="15" cy="15" r="14.5" stroke="#93959C" />
                                        <path d="M19.6052 8.50595L19.6051 8.5059C18.4708 7.31164 16.9517 6.59756 15.3391 6.50928C13.7265 6.42098 12.1437 6.96541 10.8962 8.03085C9.64939 9.09565 8.82625 10.6044 8.57844 12.2629C8.33065 13.9213 8.67438 15.6193 9.54703 17.0305L9.54705 17.0305L13.7183 23.7743L13.7183 23.7743C13.8539 23.9935 14.0411 24.1764 14.2649 24.3042C14.489 24.4321 14.7416 24.5 14.9997 24.5C15.2579 24.5 15.5105 24.4321 15.7346 24.3042C15.9584 24.1764 16.1456 23.9935 16.2812 23.7743L15.8559 23.5113L16.2812 23.7743L20.4526 17.0305C21.2624 15.7214 21.6187 14.162 21.4651 12.613C21.3116 11.064 20.6568 9.61347 19.6052 8.50595Z" stroke="#93959C" />
                                        <circle cx="15" cy="13" r="2.5" stroke="#93959C" />
                                    </svg>
                                    <div class="mt-1 white-nowrap">{{item.Location}}</div>
                                    <div >{{item.Agent}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div style="min-width: 1px; background: #33363D; height: auto">
                </div>
                <div class="col-6">
                    <b-form-group label-cols-sm="3"
                                  label="處理結果"
                                  label-align-sm="left" class="mb-4">
                        <b-form-radio-group class="pt-2 gray-text"
                                            v-model="ReportStatus">
                            <template #first>
                              <b-form-radio :value="item.value" :class="item.class" v-for="(item,index) in ReportModalData.ResultOption" :key="index">{{item.text}}</b-form-radio>
                            </template>
                        </b-form-radio-group>
                    </b-form-group>
                    <b-form-group label-cols-sm="3"
                                  label=""
                                  label-align-sm="left" class="mb-4">
                        <textarea class="CallinReportRemark w-100" placeholder="請輸入備註"  v-model="ReportRemark">
                                </textarea>
                    </b-form-group>
                    <b-form-group label-cols-sm="3"
                                  label="聯絡紀錄"
                                  label-align-sm="left" class="mb-4">
                        <div class="col-form-label ContactLog">
                            <div v-if="ContactList.status != 204">
                                <div v-for="(item,index) in ContactList.data[0]" class="mb-3" :key="index">
                                        <div>
                                            <span class="blue-text">
                                                {{item.createDT}}
                                            </span>
                                            <span class="pl-3 gray-text">
                                                {{item.status == 1 ? '已處理' : item.status == 2 ? '待再次回電' : item.status == 5 ? '聯絡失敗' : ''}} - {{item.creater}}
                                            </span>
                                        </div>
                                        <div class="mb-3 gray-text">{{item.remark}}</div>
                                </div>
                            </div>
                            <div class="gray-text" v-else>
                                尚無聯絡紀錄
                            </div>
                        </div>
                    </b-form-group>


                </div>
            </div>
            <div class="mybtn blueStyle" style=" margin: 25px auto 25px 63.5%;" @click="showMsgBoxTwo">提交</div>
        </b-modal>

        <b-modal :id="infoModal.id" ref="CallOutPopup" @hide="isCallOutFinish = false" class="CallOutPopup" centered title="" hide-footer size="sm" footer-class="model-bg-gray" content-class="model-bg-gray ReportModal">
            <div class="mb-3" v-if="CTI.CallInData">
                <div class="CallOutIcon mb-3 mt-2" :class="{Up : isCallOut,HandUp : isCallOutFinish}">
                    <img width="24" height="22" src="../assets/CallInStatusIcon.png" />
                </div>
                <div class="mb-3 text-center" v-if="isCallOutFinish">
                    通話結束
                </div>
                <div class="mb-3 flex-wrap" v-if="isCallOut == false && isCallOutFinish == false">
                    <select class="selectAgentNo" @change="onChange($event)" v-if="UserInfo.deptID1 == 'G'">
                        <option value="" selected>請選擇分機號碼</option>
                        <option :value="item.agentNo" v-for="(item,index) in MyAgentAllData" v-show="item.isCallOut == 1" :key="index">{{item.agentNo}} - {{item.location}} </option>
                    </select>
                    <select class="selectAgentNo" @change="onChange($event)" v-else>
                        <option value="" selected>請選擇分機號碼</option>
                        <option :value="item.agentNo" v-for="(item,index) in MyAgentAllData" v-show="item.isCallOut == 1 && item.location == '諮詢室'" :key="index">{{item.agentNo}} - {{item.location}}</option>
                    </select>
                </div>
                <div class="">
                    <div class="mb-3">
                        <!-- <div class="mb-2" v-if="CallInData[0]">聯絡電話 - {{CTI.CallInData[0].customerID == 0 ? '多筆資料' : CTI.CallInData[0].customerID == null ? '新客戶' : CTI.CallInData[0].name}}</div>-->
                        <div class="CallInReportBtn defaultStyle" v-if="!isEdit && isCallOutFinish == false && isCallOut == false" @click="isEdit = true">
                            編輯撥出電話
                        </div>
                        <div class="input-group input-group-sm">
                            <b-form-input v-model="infoModal.fromPhoneNo"
                                          v-if="isEdit  && isCallOutFinish == false && isCallOut == false"
                                          id="EditCellphone"
                                          placeholder="編輯撥出電話"
                                          maxlength="10"></b-form-input>
                        </div>
                    </div>
                    <div>
                        <div class="CallInReportBtn blueStyle" @click="CallOut()" v-if="isCallOut == false && infoModal.myExt !== '' && isCallOutFinish == false">
                            撥出
                        </div>
                        <div v-else-if="isCallOut == false && infoModal.myExt == '' && isCallOutFinish == false">

                        </div>
                        <div v-else-if="isCallOutFinish">
                            <div class="CallInReportBtn blueStyle" @click="$bvModal.hide('info-modal')">
                                <div>關閉</div>
                            </div>
                        </div>
                        <div class="CallInReportBtn CallOut" v-else>
                            {{infoModal.myExt}} - 撥號中
                        </div>
                    </div>
                </div>
            </div>
        </b-modal>
    </div>
</template>
<script>
    const CallOutUrl = '/API/CallOut'
    const GetCallOutReport = '/API/GetCallOut_Report0/'
   
    const ReportSubmitUrl = 'GetDataApi/ReportSubmit'
    const GetTagNameUrl = 'GetDataApi/GetTagName'
    const GetContactListUrl  = 'GetDataApi/GetContactList'

    import { mapState } from 'vuex'
    let Interval
    export default {
        naem: 'ReportCallOutV2',
        props: ['MyAgentAllData'],
        data() {
            return {
                UserInfo: '',
                isCallOut: false,
                isEdit: false,
                infoModal: {
                    id: 'info-modal',
                    fromPhoneNo: '',
                    myExt: '',
                },
                ReportModalData: {
                    IVRTitle: '',
                    CellPhone: '',
                    CallInID: '',
                    CS210LogID:'',
                    CallInData:'',
                    ResultOption: [
                        { text: '我知道了/ 已處理', value: '1',class:'green' },
                        { text: '待再次回電', value: '2',class:'red' },
                        { text: '聯絡失敗', value: '5',class:'yellow' },
                    ],
                },
                isCallOutFinish: false,
                tagName: {
                    data: [{"tagID":113,"tagName":"框片","cellphone":"0963356659"}],
                    status: '',
                },
                ContactList: {
                    data: [],
                    status: '',
                },
                ReportRemark: '',
                ReportStatus:1,
            }
        },
        mounted() {
            //取得我的分機跟員編
            let storage = JSON.parse(localStorage.getItem("UserInfo"));
            this.UserInfo = storage

            this.$on('ReportModal', function (ivrTitle, cellphone, callinID,cs210Log,dialDT) {
                this.ReportModal(ivrTitle, cellphone, callinID,cs210Log,dialDT)
            });
        },
        watch: {
        },
        computed: {
            ...mapState([ 'CTI']),
            title: function () {
                return '撥入號碼 - ' + '0988123456'
            },
            JsonAgentDial: function () {
                let Data
                if (this.CTI.AgentDial.responseData) {
                    Data = JSON.parse(this.CTI.AgentDial.responseData)
                }
                return Data
            }
        },
        methods: {
            ReportModal(ivrTitle, cellphone, callinID ,cs210Log , dialDT) {
                this.$refs['ReportModal'].show()
                this.ReportModalData.IVRTitle = ivrTitle
                this.ReportModalData.CallInID = callinID
                this.ReportModalData.CS210LogID = cs210Log
                this.ReportModalData.CellPhone = cellphone
                this.ReportModalData.dialDT = dialDT
                this.GetContactList()
            },
            onChange(event) {
                this.infoModal.myExt = event.target.value
            },
            GetContactList() {
                this.ContactList.data = []
            },
            reset() {
                this.tagName.data = []
                this.ContactList.data = []
                this.num = 'none'
                this.infoModal.myExt = ''
                this.ResetInterval()
                this.isEdit = false
                this.isCallOut = false
                this.isCallOutFinish = false
                this.$store.dispatch('handGetCallInData', '')
                this.$store.dispatch('handsetCallOutID', '')
            },
            ResetInterval() {
                clearInterval(Interval)
                this.isCallOut = false
            },

            info(fromPhoneNo, callinID) {
                this.$refs['CallOutPopup'].show()
                this.infoModal.fromPhoneNo = '0988123456'
                this.infoModal.CallInID = callinID
            },
            CallOut() {
                let AgentNo = this.infoModal.myExt
                let AccountID = this.UserInfo.accountID
                let CallOutPhoneNo = this.infoModal.fromPhoneNo
                let CallinID = this.CTI.CallInData[0].callinID
                this.isCallOut = true

                fetch(CallOutUrl, {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({ CallinID: CallinID, CallOutPhoneNo: CallOutPhoneNo, AgentNo: AgentNo, AccountID: AccountID, EventID: 1 }),
                    method: 'POST',
                },
                ).then((response) => {
                    // 可以透過 blob(), json(), text() 轉成可用的資訊
                    return response.json();
                }).then((jsonData) => {
                    let _this = this
                    if (jsonData.success) {
                        Interval = setInterval(function () {
                            _this.GetCallOutReport(CallinID)
                        }, 2000)
                    }
                }).catch((err) => {
                    console.log('錯誤:', err);
                });
            },
            showMsgBoxTwo() {
                let _this = this
                this.$bvModal.msgBoxConfirm('確認送出嗎', {
                    title: '提交回報',
                    size: 'sm',
                    buttonSize: 'sm',
                    okVariant: 'danger',
                    okTitle: '確定',
                    cancelTitle: '取消',
                    bodyClass:'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass:'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                }).then((value) => {
                    if (value) {
                        _this.ReportResultSuccessMsg()
                    }
                })
                .catch(err => {
                    // An error occurred
                })
            },
            ReportResultFailMsg() {
                this.$bvModal.msgBoxOk('提交失敗，請通知資訊人員!', {
                    title: '回報結果',
                    size: 'sm',
                    buttonSize: 'sm',
                    okVariant: 'danger',
                    okTitle: '確定',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
            },
            ReportResultSuccessMsg() {
                let _this = this
                this.$bvModal.msgBoxOk('提交成功，資料處理中請稍後', {
                    title: '回報結果',
                    size: 'sm',
                    hideFooter:true,
                    buttonSize: 'sm',
                    okTitleHtml: '',
                    okVariant:'d-none',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
            },
        }

    }
</script>
<style>
    .custom-radio.green .custom-control-label::before {
        color: #fff;
        border-color: #00A583 !important;
        background-color: transparent !important;
    }

    .custom-radio.yellow .custom-control-label::before {
        color: #fff;
        border-color: #FFDB6E !important;
        background-color: transparent !important;
    }

    .custom-radio.red .custom-control-label::before {
        color: #fff;
        border-color: #FF574F !important;
