<template>
        <div class="ItemCheckGroup">
            <input type="checkbox" :id="ID" class="d-none" name="name" :value="itemValue"  @change="PushCallInItemToArray(itemValue,reportItemType,category2)" />
            <label :for="ID" class="ItemCheck" :class="category == 'L' ? 'blue-check' : category == 'O' ? 'green-check' : category == 'Other' ? 'gray-check' : 'pink-check'">
            </label>
            <label :for="ID">
                <slot></slot>
            </label>
        </div>
</template>
<script>
    export default {
        props: ['uniqueID', 'itemValue', 'category','category2','CallInItemArray','reportItemType'],
        data() {
            return {
            }
        },
        computed: {
            ID: function() {
              return 'Btn' + this.category + this.uniqueID ;
            },
        },
        methods: {
            PushCallInItemToArray(itemValue,reportItemType,category2 ) {
                this.$emit('PushToArray', { Type: reportItemType,Value: itemValue,Cate:category2})
            }
        }
    }
</script>
<style>

    .ItemCheckGroup {
        display: flex;
        align-items: center;
        margin-bottom:20px;
        margin-right:30px;
    }

        .ItemCheckGroup label {
            margin: 0;
            cursor: pointer;
            color: #B5B7BF;
        }

        .ItemCheckGroup .ItemCheck {
            width: 20px;
            height: 20px;
            border: 1px solid #505359;
            border-radius: 5px;
            margin-right: 10px;
            transition:.3s;
        }

        .ItemCheckGroup input:checked ~ .ItemCheck.blue-check {
            background: #0090FF;
            border: 1px solid #0090FF;
        }

        .ItemCheckGroup input:checked ~ .ItemCheck.green-check {
            background: #AEE553;
            border: 1px solid #AEE553;
        }

        .ItemCheckGroup input:checked ~ .ItemCheck.gray-check {
            background: #93959C;
            border: 1px solid #93959C;
        }

        .ItemCheckGroup input:checked ~ .ItemCheck.pink-check {
            background: #E97C7C;
            border: 1px solid #E97C7C;
        }
</style>