<template>
    <b-sidebar id="DataSidebar" @hidden="resetData" sidebar-class="position-relative" right no-header shadow backdrop bg-variant="black" backdrop-variant="dark">
        <div class="position-absolute tabBtnWrap">
            <button :class="['tabBtn' , 'tabBtn01' , {active : CTI.SideBarTab == 'CustomerInfo'}]" 
                    @click="ChangeSideBarTab('CustomerInfo'),GetCustomerReportList()">
            客戶資料
            </button>

            <button ref="ReportBtn" :class="['tabBtn' , 'tabBtn02' ,{active : CTI.SideBarTab == 'Report'}]"
                    @click="ChangeSideBarTab('Report'),getCallInData(CTI.CallInID),GetAudioRecord('Callin',CTI.CallInID)">
            話務回報
            </button>
             <button ref="EIPReservationBtn" :class="['tabBtn', 'tabBtn04',{active : CTI.SideBarTab == 'EIPReservation'}]" 
                    @click="ChangeSideBarTab('EIPReservation')">
            EIP預約
            </button>
        </div>
        <component ref="sideBarData" :is="CTI.SideBarTab" ></component>
    </b-sidebar>
</template>

<script>
    import CustomerInfo from '../components/CustomerInfoV2'
    import Report from '../components/ReportV2'
    import EIPReservation from '../components/EIPReservation'
    import { mapState } from 'vuex'

    export default {
        data() {
            return {
            }
        },
        computed: {
             ...mapState(['CTI']),
        },
        components: {
            CustomerInfo,
            Report,
            EIPReservation
        },
        methods: {
            ChangeSideBarTab(tab) {
                this.$emit("ChangeSideBarTab",tab)
            },
            resetData() {
                this.$store.dispatch('handGetCustomerData', '')
                this.$store.dispatch('handGetCallInData', '')
                this.$store.dispatch('handCallOutData', '')
                this.$store.dispatch('handGetCallInID', '')
                this.$store.dispatch('handsetCallOutID', '')
            },
            getCallInData(CallInID) {
               this.$emit('getCallInData',CallInID)
            },
            GetAudioRecord(CallType,CallInID) {
                this.$emit('GetAudioRecord',CallType,CallInID)
            },
            getCallOutData(CallOutID) {
               this.$emit('getCallOutData',CallOutID)
            },
            GetCusotmerIDList(Cellphone) {
                this.$emit("GetCusotmerIDList", Cellphone)
            },
            GetCustomerReportList() {
                let _this = this
                setTimeout(() => {
                    _this.$refs.sideBarData.GetCustomerReportList(_this.CTI.CustomerData[0].sCellphone)
                }, 500)
            },
        }
    }
    

</script>

<style>
    #DataSidebar .b-sidebar-body::-webkit-scrollbar {
        height: 6px;
        background-color: transparent;
        cursor: pointer;
    }

    #DataSidebar .b-sidebar-body::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: transparent;
    }


    #DataSidebar .b-sidebar-body:hover::-webkit-scrollbar {
        height: 6px;
        background-color: transparent;
        cursor: pointer;
    }

    #DataSidebar .b-sidebar-body:hover::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: transparent;
    }



    #DataSidebar{
        width: 80%;
        background: #1D1E24;
        padding: 30px;
    }

        #DataSidebar .tabBtn {
            width: 100px;
            height: 40px;
            color: white;
            border-top-left-radius: 15px;
            border-bottom-left-radius: 15px;
            padding: 8px 20px;
            cursor: pointer;
            background: #505359;
            left: -100px;
            transition: .3s;
            border: 0;
        }


        #DataSidebar .tabBtnWrap {
            top: 30px;
            width: 100px;
            left:-100px;
        }

            #DataSidebar .tabBtnWrap button{
                margin-bottom:20px;
            }

            #DataSidebar .tabBtn.active {
                background: #0090FF;
            }

</style>