<template>
    <div class="ReportArea">
        <div class="d-flex justify-content-between align-items-center tableHeader Penal-bottom">
            <div class="Penaltitle">
                <slot name="title"></slot>
            </div>
            <div class="input-group input-group-sm">
                <b-form-input v-model="filter"
                              type="search"
                              id="filterInput"
                              class="filterInput"
                              placeholder="搜尋"></b-form-input>
            </div>
        </div>
        <b-table empty-text="暫無資料"
                 empty-filtered-text=""
                 :refs="tableName"
                 :id="tableName"
                 show-empty
                 responsive="lg"
                 :no-border-collapse="false"
                 :items="itemsTable"
                 :fields="fieldsTable"
                 :current-page="currentPage"
                 :per-page="perPage"
                 :sort-by.sync="sortBy"
                 :sort-desc.sync="sortDesc"
                 :sort-direction="sortDirection"
                 :filter="filter"
                 :filterIncludedFields="filterIncludedFields"
                 :busy="busy !== 204 && itemsTable.length == 0"
                 @filtered="onFiltered"
                 @row-hovered="rowHovered"
                 @row-unhovered="rowUnhovered">
            
            <template v-slot:table-busy>
                <div class="text-center text-danger my-2">
                    <b-spinner class="align-middle"></b-spinner>
                    <strong>Loading...</strong>
                </div>
            </template>
            <template v-slot:cell(callinID)="data">
                <div>
                   
                    # {{data.value }}
                </div>
            </template>
            <template v-slot:cell(CS210LogID)="data">
                <div>
                    # {{data.value }}
                </div>
            </template>
            <template v-slot:cell(name)="row">
                <div v-if="row.item.customerID == null" class="orange">
                    新客戶
                </div>
                <div v-else v-b-toggle.DataSidebar class="NameLink">
                    <div @click="GetCustomerData(row.item.fromPhoneNo),ChangeSideBarTab('CustomerInfo')">{{row.item.name}}</div>
                </div>
            </template>
            <template v-slot:cell(dialDT)="data">
                <span class="table-font-gray"  v-if="data.value">
                    {{ data.value.slice(5,10) }}
                    {{new Date(data.value).getDay() == 0 ? '(日)' : new Date(data.value).getDay() == 1 ? '(一)' :new Date(data.value).getDay() == 2 ? '(二)' : new Date(data.value).getDay() == 3 ? '(三)' : new Date(data.value).getDay() == 4 ? '(四)' : new Date(data.value).getDay() == 5 ? '(五)' : new Date(data.value).getDay() == 6 ? '(六)' : ''}}
                    {{ data.value.slice(11,16) }}
                </span>
            </template>
            <template v-slot:cell(soundlen)="row">
                <RecordPlayer v-if="row.item.soundlen" :recoredTime="row.item.soundlen.slice(3,10)" :callinID="row.item.callinID"></RecordPlayer>
            </template>
            <template v-slot:cell(callOutPhoneTime)="row">
                <div v-if="row.item.callOutPhoneTime">{{row.item.callOutPhoneTime.slice(3,8)}}</div>
            </template>
            <template v-slot:cell(ivrTitle)="row">
                {{row.item.ivrTitle}}
            </template>
            <template v-slot:cell(waitTime)="row">
                {{row.item.waitTime}}s
            </template>
            <template v-slot:cell(waitDay)="row">
                <div v-if="row.item.waitDay >= 3">
                    <span>
                        <svg width="7" height="6" viewBox="0 0 7 6" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <ellipse cx="3.16154" cy="3" rx="3.16154" ry="3" fill="#93959C" />
                        </svg>
                    </span>
                    {{row.item.waitDay}}天前
                </div>
                <div v-else>- -</div>
            </template>
            <template v-slot:cell(upAgent)="row">
                <div class="d-flex align-items-center" v-if="row.item.upAgent">
                    <img width="14" height="14" src="../assets/GetPhoneIcon.png" /> <span class="pl-2">{{row.item.location}}</span>
                </div>
                <div class="d-flex align-items-center" v-else>
                    <img width="14" height="14" src="../assets/NoAnswer.png" /> <span class="pl-2">未接</span>
                </div>
            </template>
            <template v-slot:cell(fromPhoneNo)="row">
                <div class="d-flex align-items-center">
                    <img width="14" height="14" src="../assets/GetPhoneIcon.png" /> <span class="pl-2">{{row.value}}</span>
                </div>
            </template>
            <template v-slot:cell(status)="row">
                <div class="d-flex align-items-center">
                    <div v-if="row.item.isBusiness" style="color: #FF955C;">
                        <svg width="7" height="6" viewBox="0 0 7 6" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <ellipse cx="3.16154" cy="3" rx="3.16154" ry="3" fill="#FF955C" />
                        </svg>
                        商機
                    </div>
                    <div v-else-if="row.item.isService" style="color: #FFDB6E;">
                        <svg width="7" height="6" viewBox="0 0 7 6" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <ellipse cx="3.16154" cy="3" rx="3.16154" ry="3" fill="#FFDB6E" />
                        </svg>
                        服務
                    </div>
                    <div v-else-if="row.item.isComplain" style="color: #FF574F;">
                        <svg width="7" height="6" viewBox="0 0 7 6" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <ellipse cx="3.16154" cy="3" rx="3.16154" ry="3" fill="#FF574F" />
                        </svg>
                        客訴
                    </div>
                    <div v-else-if="row.item.isOther" style="color: #FCFCFC;">
                        <svg width="7" height="6" viewBox="0 0 7 6" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <ellipse cx="3.16154" cy="3" rx="3.16154" ry="3" fill="#FF574F" />
                        </svg>
                        其他
                    </div>
                </div>
            </template>
            <!------------------- 未接的回報 ---------------------->
            <template v-slot:cell(actionNoAnswerReport)="row">
                <div @click="getCallInData(row.item.callinID),OpenReportModal(row.item.ivrTitle,row.item.fromPhoneNo,row.item.callinID,null,row.item.dialDT),GetWhichAgentDial(row.item.callinID)" v-if="isHovered(row.item)">
                    <div class="mr-1 reportBtn orange isHover">
                        回撥/回報
                    </div>
                </div>
                <div class="mr-1 reportBtn orange" v-else>
                    回撥/回報
                </div>
            </template>
            <!------------------- 下班的回報 ---------------------->
            <template v-slot:cell(CallinRestTimeReport)="row">
                <div @click="getCallInData(row.item.callinID),OpenReportModal(row.item.ivrTitle,row.item.fromPhoneNo,row.item.callinID,null,row.item.dialDT),GetWhichAgentDial(row.item.callinID)" v-if="isHovered(row.item)">
                    <div class="mr-1 reportBtn orange isHover">
                        回撥/回報
                    </div>
                </div>
                <div class="mr-1 reportBtn orange" v-else>
                    回撥/回報
                </div>
            </template>
            <!---------------------- 撥入的回報 ----------------------------->
            <template v-slot:cell(actionReport)="row">
                <div @click="getCallInData(row.item.callinID),GetAudioRecord('Callin',row.item.callinID),ChangeSideBarTab('Report'),GetCustomerData(row.item.fromPhoneNo)" v-if="isHovered(row.item)">
                    <div v-b-toggle.DataSidebar class="mr-1 reportBtn blue isHover">
                        回報
                    </div>
                </div>
                <div v-b-toggle.DataSidebar class="mr-1 reportBtn blue" v-else>
                    未回報
                </div>
            </template>
            <template v-slot:cell(actionFinishReport)="row">
                <div v-if="!row.item.upAgent" @click="GetReportData(row.item.callinID)">
                    <div v-if="isHovered(row.item)" v-b-modal.modal-2>
                        <div class="mr-1 reportBtn isHover">
                            回報紀錄
                        </div>
                    </div>
                    <div class="mr-1 reportBtn" v-else>
                        回報紀錄
                    </div>
                </div>
            </template>
            <template v-slot:cell(more)="row">
                <div @click="row.toggleDetails" class="mr-1 moreBtn">
                    <img src="../assets/Arrow-Down.png" />
                </div>
            </template>
            <!---------------------- 撥入的回報 ----------------------------->
            <template v-slot:cell(actionEIPReservation)="row">
                <div @click="getCallInData(row.item.callinID),GetAudioRecord('Callin',row.item.callinID),ChangeSideBarTab('EIPReservation'),GetCustomerData(row.item.fromPhoneNo)" v-if="isHovered(row.item)">
                    <div v-b-toggle.DataSidebar class="mr-1 reportBtn purple isHover">
                        新增預約
                    </div>
                </div>
                <div v-b-toggle.DataSidebar class="mr-1 reportBtn purple" v-else>
                    新增預約
                </div>
            </template>
            
            <!----------------- RWD展開Detail ---------------------->
            <template v-slot:row-details="row">
                <div class="rowDetail">
                    <div class="d-flex align-items-center">
                        <span class="rowDetailTitle">對話編號</span>#{{row.item.callinID}}
                    </div>
                    <hr />
                    <div class="d-flex align-items-center">
                        <span class="rowDetailTitle">來電號碼</span>
                        <img width="14" height="14" style="margin-right:10px;" src="../assets/GetPhoneIcon.png" />
                        <span class="rowDetailContnet">{{row.item.fromPhoneNo}}</span>
                    </div>
                    <hr />
                    <div class="d-flex align-items-center" v-if="row.item.upAgent">
                        <span class="rowDetailTitle">接聽分機</span>
                        <img width="14" height="14" src="../assets/GetPhoneIcon.png" /> <span class="pl-2">{{row.item.location}}</span>
                    </div>
                    <hr />
                    <div @click="getCallInData(row.item.callinID),GetAudioRecord('Callin',row.item.callinID),ChangeSideBarTab('Report'),GetCustomerData(row.item.fromPhoneNo)">
                        <div v-b-toggle.DataSidebar class="mr-1 reportBtn blue">
                            回報
                        </div>
                    </div>
                </div>
            </template>
        </b-table>

        <div class="d-flex justify-content-end">
            <b-pagination v-model="currentPage"
                          :total-rows="totalRows"
                          :per-page="perPage"
                          align="right"
                          class="my-0"></b-pagination>
        </div>

        <ReportCallOutV2 ref="ReportCallOutV2" :MyAgentAllData="MyAgentAllData"></ReportCallOutV2>
    </div>
</template>
<script>
    import RecordPlayer from '../components/RecordPlayer'
    import ReportCallOutV2 from '../components/ReportCallOutV2'
    export default {
        props: ['itemsTable', 'fieldsTable', 'perPage', 'sortBy', 'sortDirection', 'filterIncludedFields', 'tableName', 'busy', 'MyAgentAllData'],
        data() {
            return {
                totalRows: 1,
                currentPage: 1,
                pageOptions: [5, 10, 15],
                hoveredRow: null,
                isHover: false,
                sortDesc: true,
                filter: ' ',
                UserInfo: '',
            }
        },
        mounted() {
            this.totalRows = this.itemsTable.length
            let _this = this

            //暫時解決BUG用 初始化只能呈現一頁
            _this.resetFilter()

            //取得我的分機跟員編
            let storage = JSON.parse(localStorage.getItem("UserInfo"));
            this.UserInfo = storage
        },
        methods: {
            resetFilter() {
                let _this = this
                _this.filter = ' '
                //暫時解決BUG用 初始化只能呈現一頁
                setTimeout(function () {
                    _this.filter = ''
                }, 300)
            },
            OpenReportModal(ivrTitle, cellphone, callinID,cs210Log,dialDT) {
                this.$refs.ReportCallOutV2.$emit('ReportModal', ivrTitle, cellphone, callinID, cs210Log, dialDT)
            },
            ChangeSideBarTab(tab) {
                this.$emit("ChangeSideBarTab", tab)
            },
            onFiltered(filteredItems) {
                // Trigger pagination to update the number of buttons/pages due to filtering
                this.totalRows = filteredItems.length
                this.currentPage = 1
            },
            rowHovered(item) {
                this.hoveredRow = item;
            },
            isHovered(item) {
                return item == this.hoveredRow;
            },
            rowUnhovered(item) {
                this.hoveredRow = null
            },
            getCallInData(CallInID) {
                //撥入的回報 要先把CallOutID 變 null
                this.$store.dispatch('handsetCallOutID', null)
                this.$emit('getCallInData', CallInID)
            },
            setCallOutID(CallOutID) {
                this.$refs.CallOutPopup.hide()
                this.$store.dispatch('handsetCallOutID', CallOutID)
                this.$emit('getCallOutData', CallOutID)
            },
            GetAudioRecord(CallType,CallxxID) {
                this.$emit('GetAudioRecord', CallType,CallxxID)
            },
            imgError(item) {
                console.log(item)
                item.pictureUrl = require('../assets/DefaultPic.png')
            },
            GetReportData(CallinID) {
                this.$emit("GetReportData", CallinID)
            },
            //取得客戶資料 (用手機)
            GetCustomerData(Cellphone) {
                this.$emit("GetCustomerData", Cellphone)
            },
            GetWhichAgentDial(CallinID) {
                this.$emit("GetWhichAgentDial", CallinID)
            },
        },
        components: {
            RecordPlayer,
            ReportCallOutV2
        },
    }
</script>

<style>
    .ReportModal .ContactLog {
        overflow-y: scroll;
        height: 175px;
    }

        .ReportModal .ContactLog::-webkit-scrollbar {
            width: 5px;
            background-color: #23262B;
            cursor: pointer;
        }

    .ContactLog::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: #4c4c4c;
    }


    .ContactLog:hover::-webkit-scrollbar {
        width: 5px;
        background-color: #23262B;
        cursor: pointer;
    }

    .ContactLog:hover::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: #4c4c4c;
    }

    .pink-left-border {
        border-left: 8px solid #E97C7C;
    }

    .blue-left-border {
        border-left: 8px solid #0057FF;
    }

    .gray-left-border {
        border-left: 8px solid #93959C;
    }

    .ReportModal .modal-body {
        padding: 1.3rem;
    }

    .ReportModal .close {
        float: left;
        font-size: 1.5rem;
        font-weight: 700;
        line-height: 1;
        color: #dadada;
        text-shadow: none;
        opacity: .5;
        margin: 15px;
        position: relative;
        left: 4px;
    }

    .ReportModal button.close:after {
        width: 30px;
        height: 30px;
        content: '';
        position: absolute;
        background: #30323B;
        transform: translate(-27px, -3px);
        border-radius: 5px;
        z-index: -1;
    }

    .ReportModal .title {
        position: absolute;
        top: -23px;
        left: 63px;
    }

    .ReportModal .modal-header {
        border-bottom: 0px;
        padding-bottom: 0;
    }

        .ReportModal .modal-header .close {
            margin: -1rem auto -1rem -1rem;
        }

    .NameLink {
        text-decoration: underline;
    }

        .NameLink.blue {
            color: #0090FF;
        }

    .orange {
        color: #FF955C;
    }

    .MyExtStyle {
        color: #636363;
        border: 1px solid #636363;
        border-radius: 15px;
        padding: 4px 7px;
        display: inline-block;
        margin: 5px;
        transition: .3s;
        cursor: pointer;
        width: 100%;
        text-align: center;
    }

        .MyExtStyle.active {
            background: #505359;
            border: 1px solid #505359;
            color: #FCFCFC;
        }

    .reportBtn {
        max-width: 100px;
        width:100px;
        height: 27px;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }

        .reportBtn.blue {
            background: #0090ff1c;
            color: #0090FF
        }

            .reportBtn.blue.isHover {
                background: #0090FF;
                color: #FCFCFC;
            }

        .reportBtn.orange {
            background: #FF955C1c;
            color: #FF955C;
        }

            .reportBtn.orange.isHover {
                background: #FF955C;
                color: #FCFCFC;
            }

        .reportBtn.purple {
            background: #A637CD1c;
            color: #A637CD;
        }

            .reportBtn.purple.isHover {
                background: #A637CD;
                color: #FCFCFC;
            }
    

    .CallOutIcon {
        border-radius: 50%;
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        background-color: #0090FF;
        cursor: pointer;
    }

        .CallOutIcon.Up {
            position: relative;
            background-color: #58D786;
            z-index: 30;
        }

        .CallOutIcon.HandUp {
            position: relative;
            background-color: red;
            z-index: 30;
        }

        .CallOutIcon.Up:before {
            position: absolute;
            content: " ";
            z-index: -1;
            top: 0px;
            left: 0px;
            background-color: #58D786;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            animation-fill-mode: both;
            -webkit-animation-fill-mode: both;
            opacity: 0.6;
            -webkit-animation: pulse 1s ease-out;
            animation: pulse 1.8s ease-out;
            -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
        }

    .CallOutReportBtn {
        width: 100%;
        height: 34px;
        border-radius: 15px;
        cursor: pointer;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .CallInReportBtn {
        width: 100%;
        height: 34px;
        border-radius: 15px;
        cursor: pointer;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }

        .CallInReportBtn.Call {
            background: #0090FF;
        }

        .CallInReportBtn.SelectCallOut {
            background: #3e3e3e;
            cursor: not-allowed;
        }

        .CallInReportBtn.CallOut {
            background: none;
            cursor: not-allowed;
        }

    #EditCellphone {
        position: relative;
        background: transparent;
        border: 1px solid #505359;
        border-radius: 15px;
        width: 175px;
        height: 34px;
        padding-left: 15px;
        color: #FCFCFC;
        text-align: center;
    }

    .selectAgentNo {
        background: transparent;
        color: #FCFCFC;
        border-radius: 15px;
        width: 100%;
        padding: 5px 0;
        text-align-last: center;
        -moz-appearance: none; /* Firefox */
        -webkit-appearance: none; /* Safari and Chrome */
        appearance: none;
    }

        .selectAgentNo option {
            background: #23262B;
            box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.5);
            border-radius: 0px 0px 20px 20px;
        }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .3s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
</style>