<template>
    <div class="mr-3">
        <input class="d-none" :id="uniqueID" :type="type" :name="name" :value="KindValue" @click="OpenCateChild(KindValue)" :checked="defaultCheck"/>
        <label class="checkBtn" :class="circleStyle" :for="uniqueID">
            <slot></slot>
        </label>
    </div>
</template>
<script>
    export default {
        props:['KindValue','type','name','uniqueID','circleStyle','defaultCheck'],
        data() {
            return {

            }
        },
        methods: {
            OpenCateChild(KindValue) {
                this.$emit('OpenCateChild', KindValue)
                this.$emit('UpdateValue', KindValue)
            },
        },
        mounted() {
        }
    }
</script>
<style>
    .checkBtn {
        display:flex;
        align-items:center;
        justify-content:center;
        border: 1px solid #505359;
        border-radius: 15px;
        color: #B5B7BF;
        cursor: pointer;
        transition: .3s;
        width: 100px;
        height: 40px;
        margin-bottom:0;
    }

    .ReportItemKind:nth-child(2n+1) .mr-3 .checkBtn {
        margin-bottom: 25px;
    }

        .checkBtn:hover {
            background: #30323B;
            border: 1px solid #93959C;
            color: #B5B7BF;
        }


     input:checked ~ .checkBtn {
        background: #505359;
        border: 1px solid #505359;
        color: #FCFCFC;
    }

    label.checkBtn.defaultCircle:before {
        content: "";
        position: relative;
        width: 0px;
        height: 0px;
    }

    label.checkBtn.yellowCircle:before {
        content: "";
        position: relative;
        width: 10px;
        height: 10px;
        background: #ffb100;
        border-radius: 50%;
        margin-right: 7px;
    }

    label.checkBtn.greenCircle:before {
        content: "";
        position: relative;
        width: 10px;
        height: 10px;
        background: #00A583;
        border-radius: 50%;
        margin-right: 7px;
    }

    label.checkBtn.orangeCircle:before {
        content: "";
        position: relative;
        width: 10px;
        height: 10px;
        background: #FF955C;
        border-radius: 50%;
        margin-right: 7px;
    }

    label.checkBtn.purpleCircle:before {
        content: "";
        position: relative;
        width: 10px;
        height: 10px;
        background: #A637CD;
        border-radius: 50%;
        margin-right: 7px;
    }

    input:checked ~ label.checkBtn.yellowCircle:before {
        background: #FCFCFC;
    }

    input:checked ~ label.checkBtn.greenCircle:before {
        background: #FCFCFC;
    }

    input:checked ~ label.checkBtn.orangeCircle:before {
        background: #FCFCFC;
    }

    input:checked ~ label.checkBtn.purpleCircle:before {
        background: #FCFCFC;
    }




    input:checked ~ .checkBtn.yellowCircle {
        background: #ffb100;
        border: 1px solid #ffb100;
        color: #FCFCFC;
    }
    input:checked ~ .checkBtn.greenCircle {
        background: #00A583;
        border: 1px solid #00A583;
        color: #FCFCFC;
    }
    input:checked ~ .checkBtn.orangeCircle {
        background: #FF955C;
        border: 1px solid #FF955C;
        color: #FCFCFC;
    }

    input:checked ~ .checkBtn.purpleCircle {
        background: #A637CD;
        border: 1px solid #A637CD;
        color: #FCFCFC;
    }

        
</style>