                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import { mapState } from 'vuex'
    import ReportBtn from './ReportBtn'
    import ReportItemBtn from './ReportItemBtn'

    const ReportSubmitUrl = 'GetDataApi/ReportSubmit'
    const ReportDetailAnsSubmitUrl = 'GetDataApi/Report_Detail_AnsSubmit'
    const ReportDetailFromSubmitUrl = 'GetDataApi/Report_Detail_FromSubmit'
    const GetReportItemListUrl = 'GetDataApi/GetReportItemList'
    const GetReportFromListUrl = 'GetDataApi/GetReportFromList'
    const GetTagNameUrl = 'GetDataApi/GetTagName'
    const GetLastCellPhoneUrl = 'GetDataApi/GetLastCellPhone'

    import ReportItemList from "../JsonData/ReportItemList"
    import FromItemList from "../JsonData/FromItemList"

    export default {
        data() {
            return {
                CallInKind: [
                    { uniqueID: 'btn1', text: '新來電', value: 1, name: 'Kind', defaultCheck: 'checked' },
                    { uniqueID: 'btn2', text: '複電', value: 2, name: 'Kind', defaultCheck: '' },
                    { uniqueID: 'btn3', text: '不統計', value: 3, name: 'Kind', defaultCheck: '' },
                ],
                CallInCate: [
                    { uniqueID: 'EyeDoctorCate', text: '醫學', subtext: '醫學服務', value: 'L', class: 'blue-border' },
                    { uniqueID: 'EyeglassCate', text: '光學', subtext: '光學服務', value: 'O', class: 'green-border' },
                    { uniqueID: 'OthersCate', text: '其他', subtext: '醫學服務', value: 'Others', class: 'gray-border' }
                ],
                CallInItemKind: [
                    { uniqueID: 'btn4', text: '雷射相關', value: 'L', cate: 11, defaultCheck: 'checked' },
                    { uniqueID: 'btn5', text: '白內障', value: 'L', cate: 12, defaultCheck: '' },
                    { uniqueID: 'btn6', text: '診所相關', value: 'L', cate: 13, defaultCheck: '' },
                    { uniqueID: 'btn7', text: '醫學客訴', value: 'L', cate: 19, defaultCheck: '' },
                    { uniqueID: 'btn8', text: 'OKLENS', value: 'O', cate: 21, defaultCheck: '' },
                    { uniqueID: 'btn9', text: '金三名', value: 'O', cate: 22, defaultCheck: '' },
                    { uniqueID: 'btn10', text: '眼鏡APP', value: 'O', cate: 23, defaultCheck: '' },
                    { uniqueID: 'btn11', text: 'EYEMALL', value: 'O', cate: 24, defaultCheck: '' },
                    { uniqueID: 'btn12', text: '門市資訊', value: 'O', cate: 25, defaultCheck: '' },
                    { uniqueID: 'btn13', text: '光學客訴', value: 'O', cate: 29, defaultCheck: '' },
                    { uniqueID: 'btn14', text: 'RGP相關', value: 'O', cate: 26, defaultCheck: '' },
                    { uniqueID: 'btn19', text: '其他', value: 'Others', cate: 99, defaultCheck: '' },
                ],
                CallInFromKind: [
                    { uniqueID: 'btn21', text: '網路', value: 'L', cate: 11 },
                    { uniqueID: 'btn22', text: '平面', value: 'L', cate: 12 },
                    { uniqueID: 'btn23', text: '戶外媒體', value: 'L', cate: 13 },
                    { uniqueID: 'btn24', text: '親友介紹', value: 'L', cate: 14 },
                    { uniqueID: 'btn25', text: '其他', value: 'O', cate: 15 },
                    { uniqueID: 'btn26', text: '無', value: 'O', cate: 99 },
                ],
                CallInResultKind: [
                    { uniqueID: 'btn31', text: '已處理', value: 1, name: 'Status', class: 'greenCircle', defaultCheck: 'checked' },
                    { uniqueID: 'btn32', text: '待回CALL', value: 2, name: 'Status', class: 'yellowCircle', defaultCheck: '' },
                    { uniqueID: 'btn33', text: '須轉派', value: 3, name: 'Status', class: 'orangeCircle', defaultCheck: '' },
                    { uniqueID: 'btn34', text: 'EIP預約', value: 4, name: 'Status', class: 'purpleCircle', defaultCheck: '' },
                ],
                UserInfo: '',
                Record: [],
                ReportItemList: '',
                ReportFromList: '',
                ReportKind: 1,
                ReportRemark: '',
                ReportStatus: 1,
                CallInKindArray: [],
                CallInCateArray: [],
                CallInItemArray: [],
                CallInFromCateArray: [],
                CallInFromItemArray: [],
                Type: {
                    isBusiness: 0,
                    isComplain: 0,
                    isService: 0,
                    isOther: 0
                },
                tagName: {
                    data: [{"tagID":113,"tagName":"框片","cellphone":"0963356659"}],
                    status: '',
                },
                LastCellPhone: {
                    data: [{"tagID":1,"tagName":"2020-11-24 17:48:16","cellphone":"0963356659"}],
                    status: '',
                },
                isClinic: '',
                Reset: false
            }
        },
        watch: { //偵聽取消哪些按鈕 要消失哪些子項目
            'CallInKindArray': function (newQuestion, oldQuestion) {
                if (this.CallInKindArray.indexOf("L") == -1) {
                    this.CallInItemKind[0].defaultCheck = ''

                    this.CallInItemArray = this.CallInItemArray.filter(checkItem)
                    function checkItem(item) {
                        return item >= 2000
                    }

                    this.CallInCateArray = this.CallInCateArray.filter(checkCate)
                    function checkCate(item) {
                        return item >= 20
                    }
                }

                if (this.CallInKindArray.indexOf("O") == -1) {
                    this.CallInItemArray = this.CallInItemArray.filter(checkItem)
                    function checkItem(item) {
                        return item < 2000 || item >= 3000
                    }

                    this.CallInCateArray = this.CallInCateArray.filter(checkCate)
                    function checkCate(item) {
                        return item < 20 || item >= 30
                    }
                }

                if (this.CallInKindArray.indexOf("Others") == -1) {
                    this.CallInItemArray = this.CallInItemArray.filter(checkItem)
                    function checkItem(item) {
                        return item < 9000
                    }

                    this.CallInCateArray = this.CallInCateArray.filter(checkCate)
                    function checkCate(item) {
                        return item < 90
                    }
                }
            },
        },
        computed: {
            ...mapState(['CTI']),
            RecordUrl: function () {
                return this.CTI.AudioRecord.responseData
            },
        },
        methods: {
            RefreshPage() {
                this.$router.go()
            },
            imgError(item) {
                item.pictureUrl = require('../assets/DefaultPic.png')
            },
            UpdateReportKind(payload) {
                this.ReportKind = payload
                if (payload == 3) {
                    this.ResetSetting()
                }
            },
            ResetSetting() {
                this.CallInItemArray = this.CallInItemArray.filter(function (item) {
                    return ''
                });
                this.CallInFromItemArray = this.CallInFromItemArray.filter(function (item) {
                    return ''
                });
                this.CallInCateArray = this.CallInCateArray.filter(function (item) {
                    return ''
                });
                this.CallInFromCateArray = this.CallInFromCateArray.filter(function (item) {
                    return ''
                });
                this.CallInKindArray = this.CallInKindArray.filter(function (item) {
                    return ''
                });
                this.ReportRemark = '';
                this.ReportKind = 3;
                this.ReportStatus = 1;
            },
            UpdateReportStatus(payload) {
                this.ReportStatus = payload
            },
            OpenKindChild(index) {
                let item = this.CallInKindArray.indexOf(index);
                if (item > -1) {
                    this.CallInKindArray.splice(item, 1);
                } else {
                    this.CallInKindArray.push(index)
                }
            },
            OpenCateChild(index) {
                let item = this.CallInCateArray.indexOf(index);
                if (item > -1) {
                    this.CallInCateArray.splice(item, 1);
                    //關掉父層 子項目全部取消
                    this.CallInItemArray = this.CallInItemArray.filter(function (item) {
                        return item.Cate != index
                    });
                } else {
                    this.CallInCateArray.push(index)
                }
            },
            OpenFromChild(index) {
                let item = this.CallInFromCateArray.indexOf(index);
                if (item > -1) {
                    this.CallInFromCateArray.splice(item, 1);
                    //關掉父層 子項目全部取消
                    this.CallInFromItemArray = this.CallInFromItemArray.filter(function (item) {
                        return item.Cate != index
                    });
                } else {
                    this.CallInFromCateArray.push(index)
                }
            },
            PushCallInItemToArray(index) {
                console.log(index)
                let item = this.CallInItemArray.findIndex(x => x.Value === index['Value']);
                if (item > -1) {
                    this.CallInItemArray.splice(item, 1);
                } else {
                    this.CallInItemArray.push(index)
                }
            },
            PushCallInFromCateArray(index) {
                let item = this.CallInFromItemArray.findIndex(x => x.Value === index['Value']);
                if (item > -1) {
                    this.CallInFromItemArray.splice(item, 1);
                } else {
                    this.CallInFromItemArray.push(index)
                }
            },

            GetReportItemList() {
               this.ReportItemList = ReportItemList
            },
            GetReportFromList() {
               this.ReportFromList = FromItemList
            },
            showMsgBoxTwo() {
                let _this = this
                this.$bvModal.msgBoxConfirm('確認送出嗎', {
                    title: '提交回報',
                    size: 'sm',
                    buttonSize: 'sm',
                    okVariant: 'danger',
                    okTitle: '確定',
                    cancelTitle: '取消',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
                    .then(value => {
                        if (value) {
                            _this.ReportResultSuccessMsg()
                        }
                    })
                    .catch(err => {
                        // An error occurred
                    })
            },
            ReportResultFailMsg() {
                this.$bvModal.msgBoxOk('提交失敗，請通知資訊人員!', {
                    title: '回報結果',
                    size: 'sm',
                    buttonSize: 'sm',
                    okVariant: 'danger',
                    okTitle: '確定',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
            },
            ReportResultSuccessMsg() {
                let _this = this
                this.$bvModal.msgBoxOk('提交成功，資料處理中請稍後', {
                    title: '回報結果',
                    size: 'sm',
                    hideFooter:true,
                    buttonSize: 'sm',
                    okTitleHtml: '',
                    okVariant:'d-none',
                    bodyClass: 'model-bg-gray',
                    footerClass: 'p-2 model-bg-gray',
                    headerClass: 'model-bg-gray',
                    hideHeaderClose: false,
                    centered: true
                })
            },
            ClinicReportReset() {
                this.CallInCateArray = []
                this.CallInItemArray = []
                this.ReportRemark = []
                this.CallInKindArray = ''

            },
        },
        mounted() {
            let storage = JSON.parse(localStorage.getItem("UserInfo"));
            this.UserInfo = storage
            this.GetReportItemList()
            this.GetReportFromList()

            let myServicePhone = this.$route.params.ServicePhone
            //判斷是診所還是總公司
            if (myServicePhone == '0800668660' || myServicePhone == '0800747488') {
                this.isClinic = false
            } else {
                this.isClinic = true
                //如果是診所預設 選醫學項目
                this.CallInKindArray = 'L'
                this.CallInCateArray.push(11)
            }

        },
        components: {
            ReportBtn,
            ReportItemBtn
        }
    }
</script>
