<template>
    <div>
        <audio v-if="AudioRecord !== '' && AudioRecord !== 'fail'" controls="controls" ref="record" @pause="onPause" @play="onPlay" @timeupdate="onTimeUpdate" @loadedmetadata="onLoadedmetadata" class="d-none">
            <source :src="JSON.parse(AudioRecord.responseData)[0].fileURL.replace('https://cti.eyecenter.com.tw','')" type="audio/mp3" />
        </audio>
        <div class="recordBG download" v-if="AudioRecord == ''" style="color:white;cursor:pointer" @click="GetAudioRecord('Callin',callinID)">
            <div class="recordAnimation">
                <div id="one"></div>
                <div id="two"></div>
                <div id="three"></div>
                <div id="four"></div>
                <div id="five"></div>
                <div id="six"></div>
                <div id="seven"></div>
                <div id="eight"></div>
                <div id="nine"></div>
                <div id="ten"></div>
            </div>
            {{recoredTime}}
        </div>
         <div class="recordBG" v-else-if="AudioRecord == 'fail'" style="color:white;cursor:pointer" @click="GetAudioRecord('Callin',callinID)">
            <div class="recordAnimation">
                <div id="one"></div>
                <div id="two"></div>
                <div id="three"></div>
                <div id="four"></div>
                <div id="five"></div>
                <div id="six"></div>
                <div id="seven"></div>
                <div id="eight"></div>
                <div id="nine"></div>
                <div id="ten"></div>
            </div>
            查無
        </div>
        <div v-else>
            <div class="recordBG" @click="onPlay" v-if="!isPlay">
                <div class="recordAnimation">
                    <div id="one"></div>
                    <div id="two"></div>
                    <div id="three"></div>
                    <div id="four"></div>
                    <div id="five"></div>
                    <div id="six"></div>
                    <div id="seven"></div>
                    <div id="eight"></div>
                    <div id="nine"></div>
                    <div id="ten"></div>
                </div>
                {{maxTime == '0' ? '下載中' : maxTime | formateSecond}}
            </div>

            <div class="recordBG" :class="{active : isPlay}" @click="onPause" v-else-if="isPlay">
                <div class="recordAnimation  isHover">
                    <div id="one"></div>
                    <div id="two"></div>
                    <div id="three"></div>
                    <div id="four"></div>
                    <div id="five"></div>
                    <div id="six"></div>
                    <div id="seven"></div>
                    <div id="eight"></div>
                    <div id="nine"></div>
                    <div id="ten"></div>
                </div>
                {{currenTime | formateSecond}}
            </div>
        </div>
        
        <div style="color:white">
           
        </div>
         
    </div>
</template>
<script>
    const GetAudioRecordUrl = '/API/RecSoundInfoV2'
    import { mapState } from 'vuex'

    function realFormatSecond(second) {

         second = parseInt(second)

        let hours = Math.floor(second / 3600)

        second = second - hours * 3600

        let mimute = Math.floor(second / 60)

        second = second - mimute * 60



        return ('0' + mimute).slice(-2) + ':' + ('0' + second).slice(-2)


      
    }
    

    export default {
        props:['recoredTime','callinID'],
        data() {
            return {
                isPlay: false,
                currenTime: 0,
                maxTime: 0,
                AudioRecord:''
            }
        },
        computed: {

        },
        methods: {
            GetAudioRecord(CallType,CallxxID) {
                this.AudioRecord = ''
            },
            onPause() {
                this.isPlay = false
            },
            onPlay() {
                 this.isPlay = true
            },
            onTimeUpdate(res) {
                this.currenTime = res.target.currentTime
            },
            onLoadedmetadata(res) {
                this.maxTime = parseInt(res.target.duration)
            }
        },
        filters: {
            formateSecond(second = 0) {
                return realFormatSecond(second)
            }
        }
    }
</script>
<style>
    .recordBG .recordAnimation {
        position: absolute;
        top: -1.5px;
        height: 10px;
        left: 34px;
    }

        .recordBG .recordAnimation.isHover #one {
            animation: yuying1 0.6s infinite 0.1s;
            -webkit-animation: yuying1 0.6s infinite 0.1s;
        }

        .recordBG .recordAnimation.isHover #two {
            animation: yuying1 0.6s infinite 0.2s;
            -webkit-animation: yuying1 0.6s infinite 0.2s;
        }

        .recordBG .recordAnimation.isHover #three {
            animation: yuying1 0.6s infinite 0.3s;
            -webkit-animation: yuying1 0.6s infinite 0.3s;
        }

        .recordBG .recordAnimation.isHover #four {
            animation: yuying1 0.6s infinite 0.4s;
