        }

        .recordBG .recordAnimation.isHover #five {
            animation: yuying1 0.6s infinite 0.5s;
            -webkit-animation: yuying1 0.6s infinite 0.5s;
        }

        .recordBG .recordAnimation.isHover #six {
            animation: yuying1 0.6s infinite 0.6s;
            -webkit-animation: yuying1 0.6s infinite 0.6s;
        }

        .recordBG .recordAnimation.isHover #seven {
            animation: yuying1 0.6s infinite 0.7s;
            -webkit-animation: yuying1 0.6s infinite 0.7s;
        }

        .recordBG .recordAnimation.isHover #eight {
            animation: yuying1 0.6s infinite 0.8s;
            -webkit-animation: yuying1 0.6s infinite 0.8s;
        }


        .recordBG .recordAnimation.isHover #nine {
            animation: yuying1 0.6s infinite 0.9s;
            -webkit-animation: yuying1 0.6s infinite 0.9s;
        }

        .recordBG .recordAnimation.isHover #ten {
            animation: yuying1 0.6s infinite 1.0s;
            -webkit-animation: yuying1 0.6s infinite 1.0s;
        }

        .recordBG .recordAnimation div {
            width: 2px;
            height: 100%;
            margin-left: 3px;
            border-radius: 50px;
            background-color: gray;
            vertical-align: middle;
            display: inline-block;
        }

        .recordBG .recordAnimation.isHover div {
            background-color: #FF955C;
        }

    .recordBG {
        cursor:pointer;
        display: block;
        width: 140px;
        height: 25px;
        position: relative;
        padding-left: 90px;
        font-size: 13px;
        background: #33363D;
        border-radius: 15px;
        line-height: 25px;
        color: #D0D2D9;
    }

        .recordBG.download:before {
            left: 0px;
            top: -2px;
            width: 30px;
            height: 30px;
            position: absolute;
            content: '';
            background: url('..\assets\DownLoadIcon.png') no-repeat;
        }

        .recordBG:before {
            left: 0px;
            top: -2px;
            width: 30px;
